import React from "react";
import PropTypes from "prop-types";
import { Link, useHistory } from "react-router-dom";
import { useDispatch } from "react-redux";
import {
  forgotPasswordCall,
} from "../../../Store/Reducer/authReducer";
import { Button, Form, Input, message } from "antd";

ForgotPassword.propTypes = {
  onSubmit: PropTypes.func,
};
ForgotPassword.defaultProps = {
  onSubmit: null,
};

function ForgotPassword() {
  const dispatch = useDispatch();
  const history = useHistory();

  const handleSubmit = (val) => {
    dispatch(forgotPasswordCall({email: val.email, history}));
  };

  const onFinishFailed = (errorInfo) => {
    console.log("Failed:", errorInfo);
    message.error(errorInfo);
  };

  return (
    <div className="form">
      <div className="form__forgot-password">
        <div className="form__container">
          <Form
            layout="vertical"
            onFinish={handleSubmit}
            onFinishFailed={onFinishFailed}
          >
            <h1 className="form__title">X√°c nh·∫≠n t√†i kho·∫£n</h1>
            <div className="form__social-container"></div>
            <span>vui l√≤ng nh·∫≠p email c·ªßa b·∫°n v√†o ƒë·ªÉ t√¨m t√†i kho·∫£n</span>
            <Form.Item
              label="Nh·∫≠p Email"
              name="email"
              rules={[
                { required: true, message: "Vui l√≤ng nh·∫≠p Email c·ªßa b·∫°n!" },
                {
                  pattern: /^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/g,
                  message: "Email kh√¥ng h·ª£p l·ªá!",
                },
              ]}
            >
              <Input />
            </Form.Item>
            <div className="btn-actions">
              <Link to="/buyer/signin">
                <Button type="button" className="btn-outline-light">
                  Cancel
                </Button>
              </Link>
              <Form.Item className="btn-action">
                <Button type="primary" htmlType="submit">
                  Search
                </Button>
              </Form.Item>
            </div>
          </Form>
        </div>
      </div>
    </div>
  );
}

ForgotPassword.propTypes = {};

export default ForgotPassword;
